import "./index.scss"
import React, { Component } from "react";
import ReactDOM, { findDOMNode } from 'react-dom';
import Head from "~/components/head"

import { history } from "&"

import mylove from "../../mobx/mylove"
import { observer } from "mobx-react"
// import { get } from "mobx";
@observer
class MyLove extends Component {
    componentWillMount() {
        var mobile = sessionStorage.mobile
        console.log(mobile)
        mylove.getmylove(mobile)
        console.log(mylove.getmylove1)
        var goodsName = mylove.getmylove1.goodsName
        var mobile = sessionStorage.mobile

        // var id = this.name.value
        // // console.log(id)
        // mylove.getFlag2(id, mobile)

    }
    inMylOVE = (i) => {
        mylove.getFlag(mylove.flag)
        var flag = mylove.flag
        var mobile = sessionStorage.mobile
        var id = this[i].value
        mylove.insertMyLove(flag, id, mobile)

        mylove.getFlag2(id, mobile)
        setTimeout(() => {
            window.location.reload()

        }, 800);
    }
    gotodeclassify = () => {
        // this.props.history.push("/main/classify");

    }
    gotodetail = (i) => {
        var id = this[i].value
        mylove.gotodetail(id)
        // var goodsId = ""
        // var goodsName = ""
        // console.log(mylove.gotodetail1 )

        // // console.log(goodsName, goodsId)

    }
    change = () => {

    }
    render() {
        const {
            insertMyLove,
            gotodetail,
            gotodetail1,

            getmylove,
            getmylove1,
            flag,
            getFlag2,
        } = mylove
        // console.log( gotodetail1   )

        return (

            < div className="box" >
                <Head title="我的喜欢" show="true"></Head>
                {
                    getmylove1.length > 0 &&
                    getmylove1.map((item, i) => (
                        <div className="box_mylove" key={i}>
                            <div className="mylove_img"><img src={item.over_image_url} alt="" /></div>
                            <div className="mylove_common">
                                <p onClick={() => this.gotodetail(i)} goodsid={item._id}   >{item.goodsName}</p>
                                <input type="text" style={{ display: "none" }} ref={el => this[i] = el} value={item._id} onChange={this.change} />

                            </div>
                            <div className="setlove">
                                <p className="iconfont icon-xihuan love" onClick={() => this.inMylOVE(i)} style={item.flag && item.flag ? { color: " #ff6692" } : { color: "black" }}></p>
                            </div>
                        </div>
                    ))
                }
                {
                    !getmylove1.length > 0 &&
                    <div className="nomylove">
                        <p>您还没添加喜欢的商品呢</p>
                        <button onClick={this.gotodeclassify}>去看看</button>
                    </div>

                }

            </div >


        )
    }
}

export default MyLove

