import { observable, action, computed, autorun } from "mobx";
import { axios } from "&"
import { history } from "&"

class Mylove {
    @observable flag = ""
    @observable mylove = []
    @observable getmylove1 = []
    @observable gotodetail1 = []
    @observable goodsId = ""
    @observable goodsName = ""



    @action getFlag = () => {
        return this.flag = !this.flag
    }

    @action getFlag2 = async (id, mobile) => {
        console.log(11)
        const res = await axios.post("/react/getFlag", { id, mobile })
        return this.flag = res.data.flag

    }
    @action getmylove = async (mobile) => {

        const res = await axios.post("/react/getmylove", { mobile })
        console.log(res)
        return this.getmylove1 = res.data.result, this.flag = res.data.flag
    }


    @action insertMyLove = async (flag, id, mobile) => {

        const res = await axios.post("/react/insertmylove1", { flag, id, mobile })

        return this.mylove = res.data.result

    }

    @action gotodetail = async (id) => {

        const res = await axios.post("/react/gotodetail", { id })
        this.goodsName = res.data.result.item_name
        this.goodsId = res.data.result.item_id
        history.push(`/detail/${this.goodsId}?goodsName=${this.goodsName}`);


        //     // return goodsName, goodsId
        // })
        // return this.gotodetail1 = res.data.result

    }
}

export default new Mylove()